import React, {useState} from 'react'
import {Button, Col, Icon, MediaBox, Row} from "react-materialize";
import {useParams} from 'react-router';
import {gql, useQuery} from "@apollo/client";
import {LoadingAnimation} from '..';
import {alertsData} from "../../data"

const DetailPage = () => {
    const [colorState, setColorState] = useState(undefined)
    const [sizeState, setSizeState] = useState(undefined)
    const [bandSizeState, setBandSizeState] = useState(undefined)
    const [cupSizeState, setCupSizeState] = useState(undefined)

    const productSelectors = ["color", "size", "bandSize", "cupSize"]

    const {productId} = useParams()
    const ProductQuery = gql`       
      query ProductQuery($id: Int!) {
        productById(id: $id) {
            categories
            description
            id
            images {
              id
              url
            }
            name
            remains {
              price
              variantId
              variantName
              variantStyle
              remains
            }
            brandName
          }
        }   
    `

    const {loading, error, data} = useQuery(ProductQuery, {
        variables: {id: productId},
    })

    if (loading) return <LoadingAnimation style={{height: "50vh"}}/>
    if (error) return <h5 style={{textAlign: "center"}}>{alertsData.serverRequestFailed}</h5>
    console.log(data)
    const variants = data.productById.remains.map(remain => remain.variantStyle)
    console.log(variants)

    const chosenParameters = {
        color: colorState, size: sizeState, bandSize: bandSizeState, cupSize: cupSizeState
    }
    const appropriateVariants = []
    for (const variant of variants) {
        let isAppropriate = true
        for (const [key, value] of Object.entries(chosenParameters)) {
            if (value !== undefined && variant[key] !== value) {
                isAppropriate = false;
                break;
            }
        }
        if (isAppropriate) appropriateVariants.push(variant)
    }

    console.log(appropriateVariants)

    const availableOptions = {}
    for (const currentSelector of productSelectors) {//сменил select на currentSelector
        let available = new Set()
        for (const variant of appropriateVariants)
            if (variant[currentSelector])
                available.add(variant[currentSelector])
        availableOptions[currentSelector] = available //сменил select на currentSelector
    }

    console.log(availableOptions)

    let PressColorButton = (event) => {
        console.log("PressColorButton " + `${event.target.className}`)
        setColorState(event.target.className)
    }
    let PressSizeButton = (event) => {
        console.log("PressSizeButton " + `${event.target.innerText}`)
        setSizeState(event.target.innerText)
    }
    let PressBandSizeButton = (event) => {
        console.log("PressBandSizeButton " + `${event.target.innerText}`)
        setBandSizeState(event.target.innerText)
    }
    let PressCupSizeButton = (event) => {
        console.log("PressCupSizeButton " + `${event.target.innerText}`)
        setCupSizeState(event.target.innerText)
    }

    return (
        <Row>
            <Col
                className="black-text"
                xl={6}
                m={6}
                s={12}
            >
                {Object.values(data?.productById.images).map(image =>
                    <MediaBox
                        key={image.id}
                        options={{
                            inDuration: 250,
                            outDuration: 200
                        }}
                    >
                        <img style={{
                            marginTop: 15,
                            boxShadow: "0 2px 2px 0 rgb(0 0 0 / 14%), 0 3px 1px -2px rgb(0 0 0 / 12%), 0 1px 5px 0 rgb(0 0 0 / 20%)",
                            borderRadius: "2px"
                        }}
                             alt="Изображение товара"
                             src={image.url}
                             width="100%"
                        />
                    </MediaBox>
                )}
            </Col>
            <Col
                xl={6}
                m={6}
                s={12}
                style={{
                    position: "sticky",
                    top: 0,
                    padding: "10%",
                }}
            >
                <Row>
                    <Col
                        className="black-text"
                    >
                        <p style={{"color": "red"}}>Артикул: {productId}</p>
                    </Col>
                </Row>
                <Row>
                    <Col
                        className="black-text"
                    >
                        <h5 style={{"color": "red"}}>Название: {data?.productById.name}</h5>
                    </Col>
                </Row>
                <Row>
                    <Col
                        className="black-text"
                    >
                        <h5 style={{"color": "red"}}>Описание: {data?.productById.description} </h5>
                        Состав - материалы (Возможно можно будет описывать состав в описании, желательно отдельно)
                    </Col>
                </Row>
                <Row>
                    <Col
                        className="black-text"
                    >
                        Значек
                    </Col>
                </Row>
                <Row>
                    <Col
                        className="black-text"
                        s={1}
                    >
                        {data.productById.remains[0]?.price ?
                            <p>{"Цена:" + data.productById.remains[0]?.price}</p>
                            : <></>}
                    </Col>
                </Row>
                <Row>
                    <Col
                        xl={3}
                        l={3}
                        m={3}
                        s={3}
                    >
                        {[...new Set(availableOptions.color)].map(color =>
                            <button
                                key={color}

                                onClick={PressColorButton}
                                className={color}
                                style={{
                                    width: "40px",
                                    height: "40px",
                                    borderRadius: "50px",
                                }}
                            >
                            </button>
                        )}
                    </Col>
                    {availableOptions.size.size !== 0 ?
                        <Col
                            className="black-text"

                            xl={3}
                            l={3}
                            m={3}
                            s={3}
                        >

                            {[...new Set(availableOptions.size)].map(size =>
                                <button
                                    onClick={PressSizeButton}
                                    key={size}
                                    className={"grey lighten-2"} style={{
                                    width: "40px",
                                    height: "40px",
                                    borderRadius: "50px",
                                }}
                                >
                                    {size}
                                </button>
                            )}
                        </Col> : <></>
                    }
                    {availableOptions.cupSize.size !== 0 ?
                        <Col
                            xl={3}
                            l={3}
                            m={3}
                            s={3}
                        >
                            {[...new Set(availableOptions.cupSize)].map(cupSize =>
                                <button
                                    key={cupSize}

                                    onClick={PressCupSizeButton}
                                    className={"grey lighten-2"} style={{
                                    width: "40px",
                                    height: "40px",
                                    borderRadius: "50px",
                                }}
                                >
                                    {cupSize}
                                </button>
                            )}
                        </Col>
                        :
                        <></>
                    }
                    {availableOptions.bandSize.size !== 0 ?
                        <Col
                            xl={3}
                            l={3}
                            m={3}
                            s={3}
                        >
                            {[...new Set(availableOptions.bandSize)].map(bandSize =>
                                <button
                                    key={bandSize}

                                    onClick={PressBandSizeButton}
                                    className={"grey lighten-2"} style={{
                                    width: "40px",
                                    height: "40px",
                                    borderRadius: "50px",
                                }}
                                >
                                    {bandSize}
                                </button>
                            )}
                        </Col>
                        :
                        <></>
                    }
                </Row>
                <Row>
                    <Col
                        className="black-text"
                        s={12}
                    >
                        <Button
                            className="red"
                            node="button"
                            flat={true}
                            waves="red"
                            style={{
                                marginRight: '5px',
                                color: "white"
                            }}
                        >
                            Купить
                        </Button>
                    </Col>
                </Row>
            </Col>
        </Row>
    )
}

export default DetailPage
